@import url("https://fonts.googleapis.com/css2?family=Overpass:wght@300;400;600&family=Ubuntu:wght@400;500;700&display=swap");
.text-light-red {
  color: #FF525D;
}

.bg-light-red {
  background-color: #FF525D;
}

.text-orange {
  color: #FF856E;
}

.bg-orange {
  background-color: #FF856E;
}

.text-very-light-red {
  color: #FF7A85;
}

.bg-very-light-red {
  background-color: #FF7A85;
}

.text-white {
  color: #ffffff;
}

.bg-white {
  background-color: #ffffff;
}

.text-grayish-blue {
  color: #C8C8CB;
}

.bg-grayish-blue {
  background-color: #C8C8CB;
}

.text-very-dark-blue {
  color: #1F3F5B;
}

.bg-very-dark-blue {
  background-color: #1F3F5B;
}

.text-very-dark-grayish-blue {
  color: #4B5862;
}

.bg-very-dark-grayish-blue {
  background-color: #4B5862;
}

.text-very-dark-black-blue {
  color: #25252D;
}

.bg-very-dark-black-blue {
  background-color: #25252D;
}

.text-very-dark-desaturated-blue {
  color: #3F4164;
}

.bg-very-dark-desaturated-blue {
  background-color: #3F4164;
}

html {
  font-style: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  color: inherit;
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

body {
  font-family: "Poppins", sans-serif;
  position: relative;
  overflow-x: hidden;
}

.card {
  background-color: salmon;
}

.btn {
  border: none;
  height: 45px;
  width: 8.25rem;
  border-radius: 6.25rem;
  font-family: "Ubuntu", sans-serif;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
}
.btn-primary {
  background: linear-gradient(90deg, #FF7A85 0%, #FF525D 100%);
  color: #ffffff;
}
.btn-white {
  background: #ffffff;
  color: #FF525D;
  transition: 0.3s;
}
.btn-white:hover {
  background: #FF7A85;
  color: #ffffff;
}
.btn-border {
  background: transparent;
  color: #ffffff;
  border: 0.125rem solid #ffffff;
  transition: 0.3s;
}
.btn-border:hover {
  background: #ffffff;
  color: #FF525D;
}

.atf {
  background: linear-gradient(90.17deg, #FF856E 0.18%, #FF525D 77.03%);
  max-width: 100vw;
  background-position: left center;
  position: relative;
  height: 85vh;
  border-radius: 0 0 0 6.25rem;
  overflow: hidden;
}

.nav {
  width: 100%;
  height: fit-content;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  max-width: 81.25rem;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  z-index: 2;
  justify-content: space-between;
  padding-top: 1.25rem;
  flex: 5;
}
@media (max-width: 1400px) {
  .nav {
    max-width: 75rem;
  }
}
.nav__left {
  display: flex;
  align-items: center;
  gap: 3.125rem;
}
.nav__left__logo {
  width: 6.25rem;
}
.nav__left__menu {
  display: flex;
  gap: 2.5rem;
  font-family: "Ubuntu", sans-serif;
  font-size: 1rem;
}
.nav__left__menu li {
  position: relative;
  cursor: pointer;
}
.nav__left__menu li:after {
  position: absolute;
  right: -0.9375rem;
  bottom: 0.0625rem;
  content: url(assets/images/icon-arrow-light.svg);
}
.nav__left__menu li p:hover {
  text-decoration: underline;
}
.nav__right {
  display: flex;
  align-items: center;
  gap: 1.875rem;
}
.nav__right a {
  font-family: "Ubuntu", sans-serif;
}
.nav__right a:hover {
  text-decoration: underline;
}

.hero {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.hero__background {
  position: absolute;
  bottom: -150vh;
  left: -30vw;
  z-index: 0;
}
@media (max-width: 1400px) {
  .hero__background {
    bottom: -190vh;
    left: -35vw;
  }
}
.hero__content {
  font-family: "Overpass", sans-serif;
  text-align: center;
  z-index: 2;
}
.hero__content h1 {
  font-size: 4.25rem;
  font-weight: 500;
}
.hero__content p {
  font-size: 1.5rem;
  margin-bottom: 2.5rem;
}
.hero__cta {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.designed {
  padding-top: 7.5rem;
  width: 100%;
  text-align: center;
  max-width: 81.25rem;
  margin: auto;
  margin-bottom: 15.625rem;
}
@media (max-width: 1400px) {
  .designed {
    max-width: 75rem;
  }
}
.designed__title {
  font-size: 2.375rem;
  font-weight: 400;
  font-family: "Overpass", sans-serif;
  margin-bottom: 6.25rem;
}
.designed__content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  text-align: left;
  position: relative;
}
.designed__content__text {
  font-family: "Overpass", sans-serif;
  display: flex;
  flex-direction: column;
  gap: 4.375rem;
}
.designed__content__text h2 {
  font-weight: 400;
  font-size: 1.6875rem;
  margin-bottom: 1.25rem;
}
.designed__content__text p {
  line-height: 1.875rem;
  max-width: 31.25rem;
}
.designed__content__image {
  position: absolute;
  right: -23.5vw;
  top: -15rem;
  z-index: -1;
}

.state-phone {
  background: linear-gradient(90deg, #2F3044 0%, #3D4061 100%);
  position: relative;
  display: flex;
  align-items: center;
  height: 25.625rem;
  margin-bottom: 16.875rem;
  border-radius: 0 6.25rem;
}
.state-phone__content {
  display: flex;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  max-width: 87.5rem;
}
@media (max-width: 1400px) {
  .state-phone__content {
    max-width: 78.125rem;
  }
}
.state-phone__content .phone {
  position: absolute;
  left: 6%;
  top: -16%;
}
@media (max-width: 1400px) {
  .state-phone__content .phone {
    left: 2%;
  }
}
.state-phone__content__text {
  width: 100%;
  background-image: url("assets/images/bg-pattern-circles.svg");
  background-repeat: no-repeat;
  background-position: bottom 15% left -35%;
  display: flex;
  align-items: center;
  height: 100%;
  justify-content: flex-end;
}
@media (max-width: 1400px) {
  .state-phone__content__text {
    background-position: bottom 15% left -90%;
  }
}
.state-phone__content__text .text-group {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 0.625rem;
  max-width: 31.25rem;
  margin-right: 3.75rem;
  font-family: "Overpass", sans-serif;
  color: #ffffff;
}
.state-phone__content__text .text-group h1 {
  font-weight: 600;
  font-size: 2.25rem;
}
.state-phone__content__text .text-group p {
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.875rem;
}

.laptop-section {
  display: flex;
}
.laptop-section__content {
  display: flex;
  width: 100%;
  max-width: 87.5rem;
  position: relative;
  margin: auto;
  margin-bottom: 18.4375rem;
}
.laptop-section__content__illustration {
  position: absolute;
  left: -26%;
  top: -45%;
}
@media (max-width: 1400px) {
  .laptop-section__content__illustration {
    left: -21%;
  }
}
.laptop-section__content__text {
  display: grid;
  width: 100%;
  grid-template-columns: 1.15fr 1fr;
  justify-content: end;
}
.laptop-section__content__text .text-column {
  order: 2;
  width: 100%;
  max-width: 30.625rem;
  grid-column-start: 2;
  grid-column-end: -1;
  display: flex;
  flex-direction: column;
  gap: 4.375rem;
}
.laptop-section__content__text .text-column .text-group {
  font-family: "Overpass", sans-serif;
  color: #1F3F5B;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
.laptop-section__content__text .text-column .text-group h1 {
  font-size: 1.6875rem;
  font-weight: 400;
}
.laptop-section__content__text .text-column .text-group p {
  line-height: 1.875rem;
}

footer {
  background-color: #25252D;
  border-radius: 0 6.25rem 0 0;
  padding: 3.75rem 0 7.5rem 0;
}
footer .footer-content {
  width: 100%;
  max-width: 81.25rem;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
@media (max-width: 1400px) {
  footer .footer-content {
    max-width: 75rem;
  }
}
footer .footer-content__menu {
  display: flex;
  flex-direction: column;
  color: #ffffff;
  font-family: "Ubuntu", sans-serif;
  gap: 3.125rem;
}
footer .footer-content__menu h1 {
  font-weight: 700;
  font-size: 1rem;
}
footer .footer-content__menu ul {
  display: flex;
  flex-direction: column;
  gap: 0.9375rem;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}